body {
	font-family: sans-serif;
	font-size: 16px;
	line-height: 24px;
}
.content {
	max-width: 1280px;
	margin: 0 auto;
	padding: 0 30px;
}
.portfolio__item {
	height: auto;
	margin-bottom: 100px;
}
.portfolio__item:after {
	content: "";
    display: table;
    clear: both;
}

.portfolio__list {
	list-style: none;
	margin: 0;
	padding: 0;
}
.portfolio__list li {
	margin-bottom: 20px;
}

.portfolio__img {
	width: 400px;
	height: 300px;
	max-width: 100%;	
	float: left;
	margin-right: 50px;
	border: 5px solid rgba(16, 179, 179, .7);
	-webkit-background-size: cover;
	background-size: cover;
}
.portfolio__img-right {
	float: right;
	margin-right: 0;
	margin-left: 50px;
}
.portfolio__img-sport {
	background-image: url(../img/sport.jpg);
}
.portfolio__img-ideal {
	background-image: url(../img/ideal.jpg);
}
.portfolio__img-sunanapa {
	background-image: url(../img/sunanapa.jpg);
}
.portfolio__img-ultraunion {
	background-image: url(../img/ultraunion.jpg);
}
.portfolio__img-bridal {
	background-image: url(../img/bridal.jpg);
}
.portfolio__img-repair {
	background-image: url(../img/repair.jpg);
}
.portfolio__img-greenpay {
	background-image: url(../img/greenpay.jpg);
}

@media screen and (max-width: 768px) {
	.portfolio__img {
		float: none;
		margin-bottom: 50px;
		width: 100%;
		margin-left: 0;
		margin-right: 0;
	}
}